import React, { useState } from 'react'
import pending from './pending.png';
import approved from './approved.png';
import { MdOutlineExpandMore } from 'react-icons/md';
import { MdOutlineExpandLess } from 'react-icons/md';
import { CgAdd } from 'react-icons/cg';

const MainComponentIndex = () => {

    const [expanded, setExpanded] = useState(null);
    const [tabIndex, setTabIndex] = useState(1)

    const handleRowClick = (index) => {
        if (expanded === index) {
            setExpanded(null);
        } else {
            setExpanded(index);
        }
    };

    const fakeData = [
        { id: 1, applicationNo: "APP8765654343", ward: "3", applyDate: "23-March-2023", name: "Dipu", age: 23, email: "dsing24@gmail.com" },
        { id: 2, applicationNo: "APP2354345435", ward: "2", applyDate: "27-March-2023", name: "raj", age: 23, email: "dsing24@gmail.com" },
        { id: 3, applicationNo: "APP3463463466", ward: "5", applyDate: "29-March-2023", name: "Dipu", age: 23, email: "dsing24@gmail.com" }
    ]

    return (
        <>
            <div className='mx-8'>

                <div>
                    <div className="flex items-center justify-between mt-8">
                        <div>
                            <h2 className="text-3xl text-gray-800 font-semibold "> Water Module</h2>
                            <p className="mt-1 text-sm text-gray-500 ">
                                Here is list of all pending applications.
                            </p>
                        </div>
                        <div>
                            <button className="rounded-md bg-indigo-600 px-3.5 py-1.5 text-sm font-semibold leading-7 text-white hover:bg-indigo-500 ">
                                <span className='flex'><span className='mt-1 mr-2'><CgAdd size={20} /></span>Apply New Connection</span>
                            </button>
                        </div>
                    </div>

                </div>
                {/* Tab Details */}
                <div className="">
                    <ul className="max-w-screen-xl border-b  flex items-center gap-x-6 overflow-x-auto text-sm">
                        <li className={`py-2 border-b-2 ${tabIndex == 1 ? 'text-indigo-500 border-indigo-500' : 'border-white text-gray-500'}  `}>
                            <button
                                onClick={() => setTabIndex(1)}
                                role="tab"
                                className={`flex items-center gap-x-2 py-2 px-2 rounded-lg duration-150 hover:text-indigo-500 hover:bg-gray-50 active:bg-gray-100 font-medium`}
                            >
                                <img className="h-5 w-5" src={pending} alt="" />
                                Pending Applications
                            </button>
                        </li>
                        <li className={`py-2 border-b-2 ${tabIndex == 2 ? 'text-indigo-500 border-indigo-500' : 'border-white text-gray-500'}  `}>
                            <button
                                onClick={() => setTabIndex(2)}
                                role="tab"
                                className="flex items-center gap-x-2 py-2 px-2 rounded-lg duration-150 hover:text-indigo-500 hover:bg-gray-50 active:bg-gray-100 font-medium "
                            >
                                <img className="h-5 w-5" src={approved} alt="" />
                                Connection List
                            </button>
                        </li>
                    </ul>
                </div>


                {/* Table Start From Here */}

                <div>

                    <section className="container mx-auto py-4">
                        <div className="flex items-center justify-between mt-3 mb-5">
                            <div>
                                <h2 className="text-lg font-medium text-gray-800 ">
                                    {tabIndex == 1 && " No of pending applications : 5 "}
                                    {tabIndex == 2 && " No of water connections : 3 "}
                                </h2>

                            </div>

                        </div>

                        <div className='w-full'>

                            <div className='bg-gray-200 text-gray-700 py-3 font-semibold rounded-md'>
                                <div className=' flex items-center justify-between'>
                                    <p className='px-4'>Sl.</p>
                                    <p className='px-4'>Application No</p>
                                    <p className='px-4'>Ward No</p>
                                    <p className='px-4'>Apply From</p>
                                    <p className='px-4'>Apply Date</p>
                                    <p className='px-4'>status</p>
                                    <p className='px-4'></p>
                                </div>
                            </div>

                            {fakeData.map((item, index) => (
                                <div className='w-full border border-gray-300 rounded-md my-2 bg-white cursor-pointer' onClick={() => handleRowClick(index)} key={item.name} >
                                    <div className=' flex items-center justify-between border-b border-gray-100'>

                                        <p className="px-4 py-2 whitespace-nowrap ">
                                            <div className="flex items-center gap-2">
                                                {index + 1}
                                                {/* <div className="flex-shrink-0 rounded-full bg-sky-400 h-8 w-8">
                                                    <p className='ml-2 mt-1 font-semibold'>A</p>
                                                </div> */}
                                            </div>
                                        </p>
                                        <p className="px-4 py-2 whitespace-nowrap">
                                            {item.applicationNo}
                                        </p>
                                        <p className="px-4 py-2 whitespace-nowrap">
                                            {item.ward}
                                        </p>
                                        <p className="px-4 py-2 whitespace-nowrap ">
                                            Online
                                        </p>
                                        <p className="px-4 py-2 whitespace-nowrap ">
                                            {item.applyDate}
                                        </p>
                                        <p className="px-4 py-2 whitespace-nowrap ">
                                            <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                                                Paid
                                            </span>
                                        </p>
                                        <p className="px-4 py-2 whitespace-nowrap text-right text-sm font-medium">
                                            {expanded === index ? <MdOutlineExpandLess size={24} /> : <MdOutlineExpandMore size={24} />}
                                        </p>
                                    </div>

                                    <div className='flex-none bg-gray-100'>
                                        {/* This is extended area */}
                                        {expanded === index && (

                                            <div key={item.name} className="p-5 ease-in duration-300">
                                                {item.name}
                                                {item.age}
                                                {item.email}

                                                <div className='flex justify-end gap-3'>

                                                    <button className='px-3 py-1 rounded text-sm text-white bg-indigo-500 hover:bg-indigo-600'>View Application</button>
                                                    <button className='px-3 py-1 rounded text-sm text-white bg-red-500 hover:bg-red-600'>Delete</button>
                                                </div>

                                            </div>

                                        )}
                                    </div>


                                </div>
                            ))}

                        </div>

                    </section>


                </div>

            </div>


        </>
    )
}

export default MainComponentIndex


/*
Exported to -
NewDashIndex.js
*/