import React from 'react'
import SideBar from './SideBar'

const DashboardSidebarIndex = () => {

  const allMenu = {

    "mainMenu": [
      { name: 'Home', route: '/' ,"subMenu": []},
      {
        name: 'Property', route: '',
        "subMenu": [
          { name: 'Dashboard', route: '/propertyDashboard' },
          { name: 'Apply Holding', route: '/safform/new/0' },
          { name: 'Apply Mutation', route: '/safform/mu/0' },
          { name: 'Apply Bifurcation', route: '/safform/bi/0' },
          { name: 'Apply Amalgmation', route: '/safform/am/0' },
          { name: 'Payment History', route: '/PropertyAllTranscationList' },
          { name: 'Tax Calculator', route: '/tax-cal' },
        ],
      },
      {
        name: 'Water', route: '',
        "subMenu": [
          { name: 'Dashboard', route: '/view-water' },
          { name: 'Apply Connection', route: '/' },
          { name: 'Payment History', route: '/waterViewAllPaymentHistory' },
        ],
      },
      {
        name: 'Trade', route: '/view-trade',
        "subMenu": [],
      },
      {
        name: 'Advertisement', route: '/advertisement-dashboard',
        "subMenu": []
      },
      {
        name: 'Marketing', route: '/',
        "subMenu": []
      },
      { name: 'Profile', route: '/editProfile' , "subMenu": []},
    ],
  }

  return (
    <>
      <SideBar menu={allMenu} />
    </>
  )
}

export default DashboardSidebarIndex