import React from 'react'
import { useState, useEffect } from 'react'
import { NavLink, useLocation, useNavigate } from 'react-router-dom'
import Modal from 'react-modal';
import { FiAlertCircle } from 'react-icons/fi'
import '../../../Components/Common/CommonTailwind/Fonts.css'
import './SideBar.css'
import { ImCross } from 'react-icons/im'
import { GoThreeBars } from 'react-icons/go'
import logo1 from '../../../Components/Media/logo1.png'
import user from '../../../Components/Media/user.png'
import axios from 'axios'
import ApiHeader from '../../../Components/ApiList/ApiHeader';
import CitizenApplyApiList from '../../../Components/CitizenApplyApiList';
import BackendUrl from '../../../Components/ApiList/BackendUrl';
import { BiFoodMenu } from 'react-icons/bi';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: 'white',
    border: 'none'
  },
};
Modal.setAppElement('#root');


const SideBar = (props) => {

  const [dropDown1, setdropDown1] = useState(false)
  const [dropDown2, setdropDown2] = useState(false)
  const [dropDown3, setdropDown3] = useState(false)
  const [dropDown4, setdropDown4] = useState(false)
  const [navToggle, setnavToggle] = useState(true)
  const [dropDown, setdropDown] = useState(false)
  const [dropName, setdropName] = useState('')

  const [fetchData, setfetchData] = useState()
  const { api_getProfileDetails } = CitizenApplyApiList()

  let baseUrl = BackendUrl;


  useEffect(() => {
    axios.post(api_getProfileDetails, {}, ApiHeader())
      .then(function (response) {
        console.log('getdata for my profile in sidebar ....', response.data)
        if (response.data.status) {
          setfetchData(response?.data?.data)
        }
      })
      .catch(function (error) {

        console.log('getdata for my profile errorrr.... ', error);
      })
  }, [])

  const dropFun1 = () => {
    setdropDown1(!dropDown1)
    setdropDown2(false)
    setdropDown3(false)
    setdropDown4(false)
  }

  const dropFun2 = () => {
    setdropDown2(!dropDown2)
    setdropDown1(false)
    setdropDown3(false)
    setdropDown4(false)
  }

  const dropFun3 = () => {
    setdropDown3(!dropDown3)
    setdropDown2(false)
    setdropDown1(false)
    setdropDown4(false)
  }

  const dropFun4 = () => {
    setdropDown3(false)
    setdropDown2(false)
    setdropDown1(false)
    setdropDown4(!dropDown4)
  }

  const menuFalseFun = () => {
    setdropDown3(false)
    setdropDown2(false)
    setdropDown1(false)
  }

  const [modalIsOpen, setIsOpen] = useState(false);
  const location = useLocation()
  const navigate = useNavigate()

  if (location.pathname == '/login' || location.pathname == 'register') {
    return
  }
  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }
  const logOutUser = () => {
    closeModal()
    window.localStorage.removeItem('menuList')
    window.localStorage.removeItem('userName')
    window.localStorage.removeItem('roles')
    window.localStorage.removeItem('citizen_token')
    navigate('/')
  }

  const menuBtn = `block py-3 2xl:py-4 px-4 hover:text-white focus:text-white 2xl:text-base cursor-pointer text-sm `
  const dropMenuBtn = `block w-full ml-1 py-2 px-6 clear-both whitespace-nowrap text-sm hover:text-white hover:bg-[#7b5dd6] rounded-md text-sm animate__animated animate__fadeIn animate__faster `

  const mobileMenuBtn = `block py-3 px-4 hover:text-white hover:bg-[#7b5dd6] rounded-md animate__animated animate__fadeIn animate__faster `
  const mobileDropMenuBtn = ``

  const name = localStorage.getItem('citizenName')

  // TO RELOAD IF SAME LINK IS CLICKED
  const navigatePage = (targetPath) => {
    if (location.pathname === targetPath) {
      window.location.reload()
      return
    }
    return targetPath;
  }

  const dropFun = (val) => {
    setdropDown(!dropDown)
    setdropName(val)
  }

  return (
    <>
      <header className={(navToggle ? "animate__animated animate__slideInLeft animate__faster bg-[#613bcc]" : 'w-7 bg-[#613bcc] animate__animated ') + ' relative select-none transition-all duration-200 h-full rounded-tr-full md:rounded-tr-[50px] text-white mt-8'}>

        {/* =======navbar======== */}

        <div class={(navToggle ? "top-16 -right-5 animate__animated animate__fadeIn" : "top-4 left-2 animate__animated animate__fadeIn") + " flex items-center text-2xl text-indigo-600 bg-white p-2 w-max rounded-full absolute font-bold"} style={{zIndex : 999}}>
          <span className='cursor-pointer' onClick={() => {
            setnavToggle(!navToggle)
            setdropDown(false)
          }}><GoThreeBars fontSize='extraLarge' /></span>
        </div>

        {/* ======Menu========== */}
        { <div class={(navToggle ? "animate__animated animate__fadeInLeft animate__faster " : "animate__animated animate__fadeOutLeft animate__faster ") + " bg-[#613bcc] w-full inset-0 rounded-tr-[50px]"} id="mobile-menu">

          <nav id="mobile-nav" class="flex flex-col ltr:right-0 rtl:left-0 w-full top-0 py-4 ">

            <div class="mb-auto">

              {/* ========logo========== */}
              <div class="text-center px-4 mb-4">

                  <div class="text-2xl font-semibold text-gray-200 flex flex-col gap-y-6 items-start justify-center relative">
                  <span > <img src={user} alt="Profile" className='w-20 h-20 p-1 rounded-full float-right border-4 border-white -mt-10 bg-[#540d34]' /></span>
                    {/* <span > <img src={fetchData?.profile_photo == '' ? user : `${baseUrl}/${fetchData?.profile_photo}`} alt="Profile" className='w-12 h-12 rounded-full shadow-xl float-right border' /></span> */}
                    {/* <span className='ml-2 text-indigo-600 text-base'>{fetchData?.name}</span> */}
                    <span className='ml-8'>{fetchData?.name}</span>
                  </div>
                          <hr className='my-4 bg-[#714bdf] border-[#714bdf]'/>
              </div>

              {/* =====menus====== */}
              <div class=" px-4 h-[35rem] overflow-y-auto">
                <nav class="relative flex flex-wrap items-center justify-between">
                  <ul id="side-menu" class="w-full float-none flex flex-col">
                    {
                      props?.menu?.mainMenu?.map((item) => <>
                        <li className='relative cursor-pointer'>
                          <NavLink to={navigatePage(item?.route)} className={({ isActive }) => ((isActive && item?.subMenu?.length == 0) ? "bg-[#906dfd] " : " ") + `${mobileMenuBtn} ` + 'flex gap-2 items-center'} onClick={() => {
                            dropFun(item?.name)
                          }}> <span><BiFoodMenu /></span> <span>{item?.name}</span> </NavLink>

                          {(item?.subMenu?.length > 0 && (dropDown && dropName == item?.name)) && <ul class="block rounded rounded-t-none top-full py-0.5 ltr:text-left rtl:text-right mb-4 bg-[#6741d5]" >
                            {
                              item?.subMenu?.map((elem) => <>
                                <li class="relative cursor-pointer">
                                  <NavLink to={navigatePage(elem?.route)} className={({ isActive }) => (isActive ? "bg-[#906dfd] " : " ") + `${dropMenuBtn} ` + 'flex gap-2 items-center'} onClick={() => {
                                  }}><span><BiFoodMenu /></span> <span>{elem?.name}</span></NavLink>
                                </li>
                              </>)
                            }
                          </ul>}
                        </li>
                      </>)
                    }
                    <li className='relative'>
                      <button className='py-2 px-6 w-full text-sm inline-block text-center rounded leading-5 text-gray-100 bg-red-500 border border-pink-500 hover:text-gray-300 hover:bg-red-600 hover:ring-0 hover:border-red-600 focus:bg-red-600 focus:border-pink-600 focus:outline-none focus:ring-0 cursor-pointer' onClick={() => {
                        openModal()
                      }}>Log Out</button>
                    </li>
                  </ul>
                </nav>
              </div>

            </div>

          </nav>

        </div>}

      </header>

      {/* ===========MODAL========= */}
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >

        <div class="relative bg-white rounded-lg shadow-xl border-2 border-gray-50 p-10">
          <button onClick={closeModal} type="button" class="absolute top-3 right-2.5 text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center" >
            <svg class="w-5 h-5" fill="currentColor" ><path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg>
          </button>
          <div class="p-6 text-center">
            <div className='w-full flex h-10'> <span className='mx-auto'><FiAlertCircle size={30} /></span></div>
            <h3 class="mb-5 text-lg font-normal text-gray-500">Are you sure to logout ?</h3>
            <button type="button" class="text-white bg-red-600 hover:bg-red-800 focus:ring-4 focus:outline-none focus:ring-red-300 font-medium rounded-lg text-sm inline-flex items-center px-5 py-2.5 text-center mr-2" onClick={logOutUser}>
              Yes, I'm sure
            </button>

          </div>
        </div>

      </Modal>

    </>
  )
}

export default SideBar